import React from "react";
import "./brand-card.style.scss";
import BrandGo from "../../assets/brand-go.svg"


export const BrandCard = (props) =>{

    return(
        <div className="brand-card-div" style={{backgroundColor: props.color}} onClick={props.SelectFunction} >
            <div className="brand-card-row-1">
                <img className="brand-logo" src={props.Logo} alt=""/>
            </div>
            <div className="brand-card-row-2">
                <h1 className="brand-name"> {props.Name} </h1>
                <h2 className="brand-tag-line">Bida ang saya,  savor a crispy chicken with joy</h2>
                <img className="brand-go-svg" src={BrandGo} alt="go to store page"/>
            </div>
        </div>
    )
}