import React, {useState} from "react";
import { HashRouter as Router, Switch, Route } from "react-router-dom";
import "./default-screen.style.scss";

import {Home} from "../../home/home.component"
import { RestaurantPage } from "../../pages/restaurant/restaurant-page.component";
import {TopDocker} from "../../top-docker/top-docker.component"
import {LocationBar} from "../../location-bar/location-bar.component"
import {BottomDocker} from "../../bottom-docker/bottom-docker.component"
import {SearchBar} from "../../search-bar/search-bar.component"

export const DefaultScreen = () =>{
    // const [loadingState, setLoadingState] = useState(true)
    const [dockerState, setDockerState] = useState(false)

  const changeDockerState = () =>{
    setDockerState(prevState => !prevState)
  }

   return(
        <Router>
            <div className="Main">
                <div className="Main-row-1">
                    {(dockerState) ? <SearchBar ChangeState={changeDockerState} /> : <LocationBar ChangeState={changeDockerState} />}
                    <div className="Main-row-1-docker" >
                    <TopDocker />
                    </div>
                
                </div>
                <div className="Main-row-2">
                    <Switch>
                        <Route path="/" exact component={Home} />
                        <Route path="/restaurant" exact component={RestaurantPage} />
                        
                    </Switch>
                </div>
                <BottomDocker />
            </div>
        </Router>
   )
}