import React, {useState, useRef} from "react";
import "./restaurant-page.style.scss";

export const RestaurantPage = () =>{
    const [menu] = useState([
        "Burgers",
        "Rice Dishes",
        "Drinks",
        "Desserts",
        "Add-ons"
    ])

    const [menuList] = useState([
        {
            MenuSection: "Burgers",
            food: [
                {
                    Name: "Fish Burger",
                    Price: "12.00",
                    Description: "Good for 2 persons"
                },
                {
                    Name: "Chicken Burger",
                    Price: "7.00",
                    Description: "Good for 1 persons"
                },
                {
                    Name: "Quarter Pounder",
                    Price: "7.00",
                    Description: "Good for 1 persons"
                }
                
            ]
        },
        {
            MenuSection: "Rice Dishes",
            food: [
                {
                    Name: "Fried Rice",
                    Price: "15.99",
                    Description: "Good for 4 persons"
                    
                },
                {
                    Name: "Steamed Rice",
                    Price: "15.99",
                    Description: "Good for 4 persons"
                    
                },
                {
                    Name: "Chicken with Rice",
                    Price: "15.99",
                    Description: "Good for 4 persons"
                    
                },
                
            ]
        },
        {
            MenuSection: "Drinks",
            food: [
                {
                    Name: "Mango Shake",
                    Price: "15.99",
                    Description: "Good for 4 persons"
                    
                },
                {
                    Name: "Banana Shake",
                    Price: "15.99",
                    Description: "Good for 4 persons"
                    
                },
                {
                    Name: "Melon Juice",
                    Price: "15.99",
                    Description: "Good for 4 persons"
                    
                },
                
            ]
        }
    ])

  const itemRefs = {};
  const scrollTo = (id) =>{
    itemRefs[id].scrollIntoView();
  }
 


    return(
        <div className="restaurant-page-div">
            <div className="restaurant-page-menu">
                <ul className="restaurant-page-menu-ul">
                    {menuList.map((el,index) => {
                        
                        return <li onClick={()=> scrollTo(index)} 
                        className="restaurant-page-menu-li">
                            <h1 className="restauran-page-menu-text">{el.MenuSection}</h1></li>
                    })}
                    
                </ul>
            </div>


            <div className="restaurant-main-menu">
                <ul className="restaurant-page-menu-sections">
                    {menuList.map((el,index) => {
                        return <li
                         ref={el => (itemRefs[index] = el)}
                         className="restaurant-page-menu-sections-li">
                            <div className="restaurant-page-menu-sections-li-div">
                                <h1 id={el.MenuSection} className="menu-sections-li-header"> {el.MenuSection}</h1>
                                <ul className="restaurant-page-menu-food-ul">
                                    {el.food.map(item =>{
                                        return <li className="restaurant-page-menu-food-li">
                                            <div className="menu-food-li-div">
                                                <h1 className="menu-food-li-name"> {item.Name} </h1>
                                                <h1 className="menu-food-li-description"> {item.Description} </h1>
                                                <h1 className="menu-food-li-price"> Php {item.Price} </h1>
                                            </div>
                                            <button className="menu-food-li-add-btn">Add</button>
                                        </li>
                                    })}
                                </ul>
                            </div>
                        </li>
                    })}
                </ul>
            
            </div>        

           
        </div>
    )
}