import React from "react"
import {useHistory} from "react-router-dom"
import "./home.style.scss";
import {BrandCard} from "../brand-card/brand-card.component"
import Jollibee from "../../assets/jollibee.png"
import Goldilocks from "../../assets/goldilocks.png"
import MayAbohan from "../../assets/mayAbohan.png"

export const Home = () =>{
    const history = useHistory()

    const getRestaurant = (e) =>{
        e.preventDefault();
        history.push("/restaurant")

    }

    return(
        <div className="home-div">
                <div className="test-grid-1">
                    <BrandCard Logo={Jollibee} Name="Jollibee" color={"#E0163D"} SelectFunction={getRestaurant} />
                </div>
                <div className="test-grid-2">
                    <BrandCard Logo={Goldilocks} Name="Goldilocks" color={"#6E3629"} />
                </div>
                <div className="test-grid-1">
                    <BrandCard Logo={MayAbohan} Name="May Abohan" color={"#0F5900"} />
                </div>
                <div className="test-grid-2">
                    <BrandCard Logo={Goldilocks} Name="Goldilocks" color={"#6E3629"} />
                </div>
                <div className="test-grid-1">
                    <BrandCard Logo={Jollibee} Name="Jollibee" color={"#E0163D"} />
                </div>
                <div className="test-grid-2">
                    <BrandCard Logo={Goldilocks} Name="Goldilocks" color={"#6E3629"} />
                </div>
               
        </div>
    )
}