import React, {useState, useEffect} from "react";
// import {HashRouter as Router, Switch, Route} from "react-router-dom"
import "./Main.scss";
import {DefaultScreen} from "./components/pages/default-screen/default-screen.component"
import {NotLoggedIn} from "./components/pages/not-logged-in/not-logged-in.component"
import {LoadingScreen} from "./components/pages/Loading-Screen/loading-screen.component"


 export const Main = () => {
   
  const [loadingState, setLoadingState] = useState(true)
  const [userData,setUserData] = useState(localStorage.getItem("localUserData"))



  useEffect(()=>{
    setLoadingState(prevState => !prevState)
  },[])

  if (loadingState){
    return(
      <LoadingScreen />
    )

  }else{
      return (
        <div className="Main-container">
          {(userData !== null) ? <DefaultScreen />:<NotLoggedIn />}
        </div>
      )
  }
}

